<template >
    <div class="col-lg-12 mt-3 px-0">
        <div class="heading-border mb-2">
            <h5 class="h5">
                <span id="TabHeading2">Online Purchase</span>
                <small class="float-right">
                    <a href="javascript:;" class="addPurchaseLinkRows" title="" @click="showRow">
                        <i class="fa fa-plus pr-1"></i>Add Row
                    </a>
                </small>
            </h5>
        </div>
        <Form ref="AddOnlineForm">
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mt-3 " v-show="isVisible">
                <div class="row addQuoteRows" v-for="(item, id) in newDynamicField" :key="id.count">
                    <div class="col-lg-2">

                        <div class="form-group">
                            <label>Vendor:<span class="text-danger">*</span></label>
                            <div class="input-group">
                                <Field :name="'Vendor' + (index + 1)" class="w-100" :rules="{ 'required': true }"
                                    v-slot="{ errors }">
                                    <select name="Vendor" v-model="item.purchase_vendor"
                                        v-bind:class="{ 'form-control w-100': true, 'is-invalid': errors[0] }">
                                        <option value="">Select</option>
                                        <option v-for="item in venderList" :value="item.Value" :key="item">
                                            {{ item.Text }}
                                        </option>
                                    </select>

                                    <!-- <span v-if="errors[0]" :name="'Vendor' + (index + 1)" class="invalid-feedback d-block">{{
                            errors[0] }}</span> -->
                                </Field>
                            </div>

                        </div>
                    </div>
                    <div class="input-group-append" style="cursor:pointer;">
                        <span class="input-group-text border-0 bg-transparent p-0 pl-2"><a @click="Addvendor"
                                class="round-icon-small btn-dark theme-primary round-icon-small AddQuoteVendor"><i
                                    class="fa fa-plus pt-7 AddQuoteVendor " id="AddQuoteVendor" title="Add Vendor"
                                    onclick="AddVendor('quote');"></i></a></span>
                    </div>

                    <div class="col-lg-2">
                        <div class="form-group">
                            <label>Requested Quantity:<span class="text-danger">*</span></label>
                            <div class="input-group">

                                <Field name="Request Quantity" :rules="{ 'required': true }" v-slot="{ errors }">
                                    <input v-model="item.txtpurchaseQuantity" type="text"
                                        v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }" />
                                    <!-- <span v-if="errors[0]" name="Request Quantity" class="invalid-feedback d-block">{{
                                    errors[0] }}</span> -->
                                </Field>
                            </div>
                        </div>
                    </div>
                    <div class="col-md-1 ">
                        <div class="form-group">
                            <label>Currency:<span class="text-danger">*</span></label>
                            <div class="input-group">

                                <Field name="Currency" class="w-100" :rules="{ 'required': true }" v-slot="{ errors }">
                                    <select name="Currency" v-model="item.getCurrencyValue"
                                        v-bind:class="{ 'form-control w-100': true, 'is-invalid': errors[0] }">
                                        <option value="">Select</option>

                                        <option v-for="(item, currencyIndex) in currencyList" :value="item.CurrencyId"
                                            :key="currencyIndex">
                                            {{ item.Currency }}
                                        </option>
                                    </select>
                                    <!-- <span v-if="errors[0]" name="Currency" class="invalid-feedback d-block">{{
                                    errors[0] }}</span> -->
                                </Field>

                            </div>

                        </div>
                    </div>
                    <div class="col-lg-1 pl-0">
                        <div class="form-group">
                            <label>Unit Price:<span class="text-danger">*</span></label>

                            <Field name="Unit Price" :rules="{ 'required': true }" v-slot="{ errors }">
                                <input v-model="item.priceAmount" type="text" v-on:keyup="calculateTotalQuantity(item)"
                                    v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }" />
                                <!-- <span v-if="errors[0]" name="Unit Price" class="invalid-feedback d-block">{{
                                errors[0] }}</span> -->
                            </Field>


                        </div>
                    </div>
                    <div class="col-lg-1 pl-0">
                        <div class="form-group">
                            <label>Total Price:<span class="text-danger">*</span></label>
                            <input v-model="item.purchasePrice" type="text" disabled class="form-control TotalPrice" />

                        </div>
                    </div>
                    <div class="col-lg-3 " v-for="(purchase, index) in item.purchaseDynamicField" :key="'A' + index">

                        <div class="form-group">
                            <!-- <label>Purchase Link<span class="text-danger">*</span></label> -->
                            <label>Purchase Link<span class="text-danger"></span></label>

                            <div class="input-group">
                                <input type="text" @keyup="purchaseUrlData(item, purchase)" v-model="purchase.purchase_link"
                                    class="form-control" name="purchaseLink">
                                <div class="input-group-append" style="cursor:pointer;">
                                    <span class="group-span-filestyle input-group-btn input-group-text bg-grey">
                                        <a title="Click here to copy link." class="mb-0 font-weight-normal "><i
                                                class="fa fa-copy "></i></a>
                                    </span>

                                    <span class="input-group-text border-0 bg-transparent p-0 pl-2">
                                        <a href="javascript:void(0);" @click="addLinkRow(item.count)"
                                            v-if="0 == parseInt(index)"
                                            class="round-icon-small btn-dark theme-primary round-icon-small">
                                            <i class="fa fa-plus pt-7"></i>
                                        </a>
                                        &nbsp;

                                        <a href="javascript:void;" @click="removeRow(item, purchase);"
                                            class="btn btn-danger theme-primary removefield">
                                            <i class="fa fa-times pt-7" title=""></i>
                                        </a>

                                    </span>

                                </div>
                            </div>

                        </div>
                    </div>

                </div>

            </div>
        </Form>
        <AddVendorPopUp v-if="isShowvendor"></AddVendorPopUp>

    </div>
</template>
<script>
import DataService from "../../service/DataService";
import AddVendorPopUp from '../../components/Modal/AddVendorModel.vue'

export default {
    components: {
        AddVendorPopUp
    },

    data() {
        return {
            isShowvendor: false,
            isVisible: false,
            newDynamicField: [{
                purchase_vendor: '',
                txtpurchaseQuantity: '',
                getCurrencyValue: '',
                purchasePrice: '',
                priceAmount: '',
                count: 1,
                purchaseDynamicField: [
                    {
                        purchase_link_id: 0,
                        purchase_link: '',
                        pcount: 1
                    }
                ],
            }

            ],

            specificCount: null,
            venderList: [],
            currencyList: [],


            purchaseData: {}
        }
    },

    created() {
        var vm = this;
        vm.getVendorlist();
        vm.getCurrencylist();

    },
    methods: {

        getVendorlist() {

            DataService.GetCrmVendor().then((response) => {

                if (response.data != null) {
                    this.venderList = response.data;
                }
            })

        },

        getCurrencylist() {

            DataService.GetCurrency().then((response) => {
                if (response.data != null) {
                    this.currencyList = response.data;
                }
            })
        },

        Addvendor() {
            this.isShowvendor = true;
        },

        CloseAddVendorModel() {
            var vm = this;
            vm.isShowvendor = false;
        },

        calculateTotalQuantity(item) {


            var vm = this;
            vm.specificCount = item.count;

            vm.newDynamicField.forEach(element => {

                if (element.count == vm.specificCount) {
                    element.purchasePrice = item.txtpurchaseQuantity * item.priceAmount;
                }

            })


            const getRecordInformation = vm.newDynamicField.find(x => x.count === vm.specificCount);
            if (getRecordInformation != null) {

                vm.purchaseData = {
                    purchase_id: 0,
                    purchase_vendor: getRecordInformation.purchase_vendor,
                    purchaseQuantity: getRecordInformation.txtpurchaseQuantity,
                    purchasePrice: getRecordInformation.getCurrencyValue,
                    ppriceAmount: getRecordInformation.priceAmount,
                    purchaseTotalPrice: getRecordInformation.purchasePrice,
                    purchaselinks: getRecordInformation.purchaseDynamicField
                }

                vm.$emit('purchases', vm.purchaseData);
            }



        },


        purchaseUrlData(item, purchase) {

            var vm = this;
            const getRecordInformation = vm.newDynamicField.find(x => x.count === item.count);
            if (getRecordInformation != null) {

                vm.purchaseData = {
                    purchase_id: 0,
                    purchase_vendor: getRecordInformation.purchase_vendor,
                    purchaseQuantity: getRecordInformation.txtpurchaseQuantity,
                    purchasePrice: getRecordInformation.getCurrencyValue,
                    ppriceAmount: getRecordInformation.priceAmount,
                    purchaseTotalPrice: getRecordInformation.purchasePrice,
                    purchaselinks: getRecordInformation.purchaseDynamicField
                }

                vm.$emit('purchases', vm.purchaseData);
            }


        },


        showRow() {
            debugger;
            var vm = this;

            vm.$refs.AddOnlineForm.validate().then(result => {

                if (vm.isVisible == false) {
                    return vm.isVisible = true;
                }
                if (result) {

                    let singleDynamicRow = {
                        purchase_vendor: '',
                        txtpurchaseQuantity: '',
                        getCurrencyValue: '',
                        purchasePrice: '',
                        priceAmount: '',
                        count: 1,
                        purchaseDynamicField: [
                            {
                                purchase_link_id: 0,
                                purchase_link: '',
                                pcount: 1
                            }
                        ],
                    }

                    singleDynamicRow.count = vm.newDynamicField.length + 1;
                    vm.newDynamicField.push(singleDynamicRow);

                } else {
                    vm.ShowAlert(vm.$t("Please correct the highlighted errors"), "failure", vm.$t('Alert'));

                }

            })


        },

        addLinkRow(count) {
            debugger;
            var vm = this;
            vm.specificCount = count;
            vm.isVisible = true;
            let purchase = {};


            const getRecordInformation = vm.newDynamicField.find(x => x.count === vm.specificCount);
            if (getRecordInformation != null) {

                purchase = {
                    purchase_link_id: 0,
                    purchase_link: '',
                    pcount: vm.newDynamicField[0].purchaseDynamicField.length + 1

                }

                vm.newDynamicField.forEach(ele => {

                    if (ele.count == vm.specificCount) {
                        ele.purchaseDynamicField.push(purchase);
                    }
                })
            }

            vm.purchaseData = {
                purchase_id: 0,
                purchase_vendor: getRecordInformation.purchase_vendor,
                purchaseQuantity: getRecordInformation.txtpurchaseQuantity,
                purchasePrice: getRecordInformation.getCurrencyValue,
                ppriceAmount: getRecordInformation.priceAmount,
                purchaseTotalPrice: getRecordInformation.purchasePrice,
                purchaselinks: getRecordInformation.purchaseDynamicField
            }

            vm.$emit('purchases', vm.purchaseData);
        },


        removeRow(item, purchase) {

            var vm = this;
            let newDynamicArrayCount = item.count;

            vm.newDynamicField.forEach((item) => item.purchaseDynamicField.forEach((subItem, index) => {

                if (item.purchaseDynamicField.length === 1) {

                    const index = vm.newDynamicField.findIndex(x => x.count === newDynamicArrayCount);
                    if (index !== -1) {
                        vm.newDynamicField.splice(index, 1);
                    }
                }

                else if (subItem.pcount === purchase.pcount) {
                    return item.purchaseDynamicField.splice(index, 1);
                }

            }));

        }


    }


}


</script>
<style ></style>