<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title">
                                {{ $t('Add vendor') }}
                            </h5>
                            <button v-on:click="closePopup()" type="button" class="close" title="Close"><em
                                    class="fa fa-times"></em></button>
                        </div>
                        <div class="modal-body">
                                <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                                    v-on:OnSubmit="Addvendor">
                                </dynamic-form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
    
</template>
<script>
import DataService from '../../service/DataService';
import AddQuotes from '../../views/AssetRequisition/AddQuotes.vue';
export default {
    components: {
       AddQuotes
    },
    data() {
        return {
            CountryData:[],
            FormSchema: [
                        {
                            layoutType: "double",
                            Data: [
                                    {
                                    astype: "TextField",
                                    label: this.$t("Vendor"),
                                    name: "Vendor",
                                    value: '',
                                    placeholder: "",
                                    validationRules:{"required":true},
                                    
                                },
                                {
                                    astype: "TextField",
                                    label: this.$t('Contact Person'),
                                    name: "ContactPerson",
                                    value: "",
                                    validationRules: { "required": false,},
                                    placeholder: ""  ,
                                },
                            ]
                        },
                        {
                            layoutType:"Single",
                            Data:[
                            {
                                    astype: "TextAreaField",
                                    label: this.$t("Description"),
                                    name: "Description",
                                    value: '',
                                    placeholder: "",
                                    validationRules:{"required":false},
                                    
                                },
                            ]
                        },
                        {
                            layoutType: "double",
                            group_name:'Product Type',
                            Data: [
                                    {
                                    astype: "TextField",
                                    label: this.$t("Street"),
                                    name: "Street",
                                    value: '',
                                    placeholder: "",
                                    validationRules:{"required":false},
                                    
                                },
                                {
                                    astype: "TextField",
                                    label: this.$t('State'),
                                    name: "State",
                                    value: "",
                                    validationRules: { "required": false,},
                                    placeholder: ""  ,
                                },
                            ]
                        },
                        {
                            layoutType: "double",
                            Data: [
                                    {
                                    astype: "TextField",
                                    label: this.$t("City"),
                                    name: "City",
                                    value: '',
                                    placeholder: "",
                                    validationRules:{"required":false},
                                    
                                },
                               {
                            astype: "SelectField",
                            label: this.$t('Country'),
                            name: "Country",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": false},
                            config: {
                                        options: [                        
                                            
                                        ],
                                                                      
                                    },
                        },
                            ]
                        },
                        {
                            layoutType: "double",
                            Data: [
                                    {
                                    astype: "TextField",
                                    label: this.$t("Zip code"),
                                    name: "Zipcode",
                                    value: '',
                                    placeholder: "",
                                    validationRules:{"required":false},
                                    
                                },
                                {
                                    astype: "TextField",
                                    label: this.$t('Phone Number'),
                                    name: "Phonenumber",
                                    value: "",
                                    validationRules: { "required": false,},
                                    placeholder: ""  ,
                                },
                            ]
                        },
                          
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },

                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.closePopup,
                }
            ],  
        }
    },
    created (){
        this.GetCountryDDL();

    },
    methods:{

        Addvendor:function(formfields){  
            debugger;
            var vm=this;
            var jsonobj={
                Vendor: formfields.Vendor,
                vendorOwner: formfields.ContactPerson,
                Description: formfields.Description,
                Street: formfields.Street,
                state: formfields.State,
                city: formfields.City,
                country: formfields.Country,
                zipcode: formfields.Zipcode,
                phone_number: formfields.Phonenumber
            }                    
            var poststring=JSON.stringify(jsonobj);
            DataService.Addvendor(poststring).then(response=>{
                debugger;                    
            if(response.data.data.status=='insert'){
                 vm.ShowAlert(vm.$t('AddedSuccessvendor',[vm.$t('Group_Item')]), "success", true,  vm.$t("Alert")); 
                vm.closePopup();
            }
            else{
                vm.ShowAlert(vm.$t("Vendorexist"), "failure", true, vm.$t('Alert'));
                vm.closePopup();
            } 
            vm.$parent.getVendorlist();
        });  
        },
        closePopup() {
            var vm = this;
            vm.$parent.CloseAddVendorModel();
        },
        GetCountryDDL:function(){
            debugger
            var url =`moduleName=CRM&type=COUNTRIES`;
            DataService.GetCountryDDL(url).then(result=>{
                debugger
                this.CountryData=result.data.DATA;
                //var obj={};
                this.CountryData.forEach((item)=>{
                    var obj={
                        name :  item.name,
                        value : item.value
                    };
                    (this.FormSchema)[3].Data[1].config.options.push(obj);
                });
                
                debugger
            });
        }
       
    }

}
</script>